body {
  font-family: Arial, sans-serif;
  background:url(https://as1.ftcdn.net/v2/jpg/01/05/59/48/1000_F_105594803_ekPRfmQCqh5PBqYzceevGhRjpGc0Lhlq.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  padding: 20px;
}

.App {
  max-width: 600px;
  margin: 0 auto;
  background: ghostwhite;
  padding: 20px;
  border-radius: 8px;

}

h1 {
  text-align: center;
  color: rgb(194, 55, 45);
  font-style: italic;
}

input[type="text"] {
  width: 80%;
  padding: 10px;
  margin-bottom: 10px;
  border: 1px solid black;
  border-radius: 5px;
}

button {
  padding: 10px 15px;
  margin: 5px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  background-color: red;
  color: white;
}

button:hover {
  background-color: rgb(101, 69, 69);
}

ul {
  list-style: none;
  padding: 0;
}

li {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 5px;
  border-bottom: 1px solid black;
}

li.completed span {
  text-decoration: line-through;
  color: black;
}
